.loadScreen {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #000000cc;
    z-index: 3;
    pointer-events: none;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    > svg {
        width: 70%;
        max-width: 500px;
        background-color: #e4f8f8;
        border-radius: 20px;

        #load {
            &_cloud {
                stroke-width: 30px;
                stroke-linecap: round;
                &--1 {
                    stroke: #a4cfb6;
                }
                &--2 {
                    stroke: #c0e4ce;
                }
            }

            &_car {
                fill: #60b9ff;
                stroke: transparentize(black, 0.5);
                stroke-width: 0.5px;

                &_windows {
                    fill: #00000040;
                }

                &_smoke {
                    fill: grey;
                    stroke: none;
                }

                &_wheel {
                    &_outer {
                        fill: black;
                    }
                    &_inner {
                        fill: grey;
                    }
                    stroke: black;
                }
            }
        }
    }
}
